import React,{useEffect} from 'react'
import {connect} from 'react-redux'
import './Detail.css'
import {getContent,getSubcategory} from '../redux/action/content'
const Detail = ({match,getContent,getSubcategory,contents:{Allcontent,loading,subcategory}}) => {

    useEffect(()=>{
        getContent()
        getSubcategory()
    },[])

    let A = [...Allcontent]
    let B = [...subcategory]
    let filterdata =  A.filter((el)=>el.subcategory.title === match.params.title );
    let TITLE =  B.filter((el)=>el.title === match.params.title );
    return (
        <div className='Detail'>
            {TITLE.map((title)=>
            <div style={{width:'auto'}} className='Judul'> 
                <h1>{title.title}</h1>
            </div>
            )}
            
            {filterdata.map((Content)=>
            <div className='Container'>
                <div className='thumbnail'>
                    <img src={Content.img}/>
                </div>
                <div className='text'>
                    <p style={{textAlign:'justify'}}>{Content.text}</p>
                </div>
            </div>
            )}       
        </div>
    )
}

const mapStateToProps = (state)=>({
    contents: state.content
})
export default connect(mapStateToProps,{getContent,getSubcategory})(Detail) ;
