import React,{useEffect, useState} from 'react'
import { Editor } from 'react-draft-wysiwyg';
import { EditorState } from 'draft-js';
import {getSubcategory} from '../redux/action/content'
import {postContent}from '../redux/action/content'
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import './EditorText.css'
import Title from '../components/addSubCategory/AddSubCategory'
import {connect} from 'react-redux'
import {loadUser} from '../redux/action/auth'


const EditorText = ({getSubcategory,postContent,loadUser,contents:{Allcontent,loading,subcategory},auths:{user}}) => {
  const [Slide,setSlide]=useState(false)

  let B = [...subcategory] 
  let filtered = B.filter((el)=>el.userId === user.id );
  useEffect(()=>{
    getSubcategory()
    loadUser()
  },[Slide])

  const [formData, setFormData] = useState({
    subcategoryId: "",
    text:""
  });

  const {text,subcategoryId} = formData;

  const onChange = (e) => {
      setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const onSubmite = (e) =>{
      e.preventDefault();
      const category = subcategoryId
      postContent(`${user.id}`,text,category)
      setFormData({
        text:""
      })
  }
  return (
    <div>
        <div style={{paddingTop:100,paddingBottom:100}}>

          <div style={{paddingLeft:'80%'}}>
            <button onClick={()=>setSlide(!Slide)} style={{borderRadius:'60%',height:45,backgroundColor:'#3399FF',border:'none',fontWeight:'bold',color:'white'}}>Slide</button>
          </div>

          {Slide == false && <Title userId={user.id}/>}

          <form onSubmit={(e) => onSubmite(e)}>
            <div className="editor-wrapper">
              {Slide == true &&
                  <select
                    className="custom-select"
                    name='subcategoryId'
                    onChange={(e) => {
                        onChange(e);
                    }}
                  >
                  <option value={subcategoryId}>Select Judul</option>
                          {loading ? (<option>WIAT FOR LOADING</option>):(
                              filtered.map((Allcategory) => (
                                  <option value={Allcategory.id} key={Allcategory.id}>
                                      {Allcategory.title}
                                  </option>
                              ))
                          )}
                  </select>}
            </div>
            <div className='editor-wrapper'>
              <textarea
              placeholder='Write Your Story here'
              name='text'
              className='input'
              style={{height:200,paddingTop:20}}
              value={text}
              onChange={(e)=>onChange(e)}
              />
              <div className='btn-custom'>
                <button>POST</button>
              </div>
            </div>
          </form>
        </div>
    </div>
    
  )
}
const mapStateToProps = (state)=>({
  contents: state.content,
  auths: state.auth
})
export default connect(mapStateToProps,{getSubcategory,postContent,loadUser})(EditorText) ;
